import React, { useEffect } from 'react'
import styles from './Header.module.css'
import { NavLink } from 'react-router-dom'
import logo from './../../images/logo.png'
import entrance from './../../images/entrance.png'
import { useSelector, useDispatch } from 'react-redux'
import { setLogout } from '../../redux/auth-reducer'

// import { useSelector, useDispatch } from 'react-redux'



const Header = (props) => {
    const userIsAuth = useSelector(store => store.loginPage.userIsAuth)
    const adminIsAuth = useSelector(store => store.loginPage.adminIsAuth)
    const userName = useSelector(store => store.loginPage.userName)
    const approved = useSelector(store => store.loginPage.approved)
    const dispatch = useDispatch()


    return (
        <div className={styles.headerWrapper}>
            <div className={styles.firstHeader}>
                <div className={styles.logo}>
                    <img src={logo} className={styles.logoImg} width = '200px'/>
                </div>
                {!userIsAuth ? <div className={styles.infoEntrance}>
                    <NavLink to='/login'><img src={entrance} /></NavLink>
                    <div className={styles.infoEntranceDetail}>–í—Ö–æ–¥ –≤ –∫–∞—Ç–∞–ª–æ–≥</div>
                </div>
                    : <div className={styles.autorizedHeader}>
                        <div className={styles.firstHeaderColumn}>
                            <div>{userName}</div>
                            <div>{approved?'–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∞':'–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è –Ω–µ –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∞'}</div>
                            <div>{adminIsAuth?'–ê–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä': null}</div>
                        </div>
                        <div className={styles.infoEntrance}>
                            <img src={entrance} onClick = {() =>dispatch(setLogout())}/>
                            <div className={styles.infoEntranceDetail}>–í—ã–π—Ç–∏ –∏–∑ –∫–∞—Ç–∞–ª–æ–≥–∞</div>
                        </div>
                    </div>


                }


            </div>
            <div className={styles.secondHeader}>
                <div className={styles.navBar}>
                <div className={styles.navigationLinkTop}><a className={styles.links} href ='http://perekupi.ru/catalog/banknoty/'>–ë–∞–Ω–∫–Ω–æ—Ç—ã</a></div>
                    <div className={styles.navigationLinkTop}><NavLink to = '/catalog' className={styles.links} >–ö–∞—Ç–∞–ª–æ–≥</NavLink></div>
                    {/* <a className={styles.links}>–ù–∞—à–∏ –∫–æ–Ω—Ç–∞–∫—Ç—ã</a> */}
                    {adminIsAuth
                    ?<div style={{display: 'flex', width: '30%', justifyContent: 'space-between'}}>
                        
                    <div className={styles.navigationLinkTop}><NavLink to = '/requests'>–ó–∞–ø—Ä–æ—Å—ã</NavLink></div></div>: null}
                    
                </div>
            </div>
            <div className={styles.thirdHeader}>
                <div className={styles.infoNav}>
                    <div className={styles.links}><a style={{ textDecoration: 'none', color: '#999999' }} href='http://perekupi.ru/'>Perekupi.ru</a></div>
                    <div className={styles.links}><div className={styles.navigationLink}><NavLink to='/catalog'>/{' '}–≠–ª–µ–∫—Ç—Ä–æ–Ω–Ω—ã–π –∫–∞—Ç–∞–ª–æ–≥</NavLink></div></div>
                    <div className={styles.links}>/{' '}{props.currentPage}</div>
                </div>
            </div>
        </div>
    )
}

export default Header