import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { changeDescriptionCells } from '../../../redux/admin-catalog-reducer';
import Button from '@material-ui/core/Button';
import Krutilka from './../../../images/preloader.gif'

const AdminEditMode = ({ description }) => {
  const buttonDisable = useSelector(store => store.AdminCatalog.buttonDisable)
  const dispatch = useDispatch()
  const deactivateEditModeRequest = () => {
    dispatch(changeDescriptionCells(description.id, description.year, description.nominal, description.number, description.upravl, common,varieties,astimation,kassirInfo,sourses,status, image1,image2 ))
  }

  let [status, setNewStatus] = useState(description.status)
  const onStatusChange = (e) => {
      setNewStatus(e.currentTarget.checked)
  }

  let [common, setNewCommon] = useState(description.common)
  const onCommonChange = (e) => {
    setNewCommon(e.currentTarget.value)
  }

  let [varieties, setNewVarieties] = useState(description.varieties)
  const onVarietiesChange = (e) => {
    setNewVarieties(e.currentTarget.value)
  }

  let [astimation, setNewAstimation] = useState(description.astimation)
  const onAstimationChange = (e) => {
    setNewAstimation(e.currentTarget.value)
  }

  let [kassirInfo, setNewAddInfo] = useState(description.kassirInfo)
  const onAddInfoChange = (e) => {
    setNewAddInfo(e.currentTarget.value)
  }


  let [sourses, setNewSourses] = useState(description.sourses)
  const onSoursesChange = (e) => {
    setNewSourses(e.currentTarget.value)
  }
  // –ó–∞–≥—Ä—É–∑–∫–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π start
  const [image1, setImage1] = useState('')
  const [image2, setImage2] = useState('')
  const [loading, setLoading] = useState(false)
  
  const uploadImage1 = async e => {
    const files = e.target.files
    const data = new FormData()
    data.append('file', files[0])
    data.append('upload_preset', 'jh7fsjhx')
    setLoading(true)
    const res = await fetch(
      'https://api.cloudinary.com/v1_1/drkhbnvbo/image/upload',
      {
        method: 'POST',
        body: data
      }
    )
    const file = await res.json()
    setImage1(file.secure_url)
    setLoading(false)
  }

  const uploadImage2 = async e => {
    const files = e.target.files
    const data = new FormData()
    data.append('file', files[0])
    data.append('upload_preset', 'jh7fsjhx')
    setLoading(true)
    const res = await fetch(
      'https://api.cloudinary.com/v1_1/drkhbnvbo/image/upload',
      {
        method: 'POST',
        body: data
      }
    )
    const file = await res.json()
    setImage2(file.secure_url)
    setLoading(false)
  }
  //–∑–∞–≥—Ä—É–∑–∫–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π end

  return <div >
    <div><input type = "checkbox"  value ={status} onChange = {onStatusChange}/> –°—Ç–∞—Ç—É—Å –∑–∞–ø–∏—Å–∏</div>
    –û–ø–∏—Å–∞–Ω–∏–µ: <textarea style={{ width: '90%' }} autoFocus={true} onChange={onCommonChange}
      value={common} />
    –†–∞–∑–Ω–æ–≤–∏–¥–Ω–æ—Å—Ç–∏: <textarea style={{ width: '90%' }} autoFocus={true} onChange={onVarietiesChange}
      value={varieties} />
    –û—Ä–∏–µ–Ω—Ç–∏—Ä–æ–≤–æ—á–Ω–∞—è —Å—Ç–æ–∏–º–æ—Å—Ç—å: <textarea style={{ width: '90%' }} autoFocus={true} onChange={onAstimationChange}
      value={astimation} />
    –î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–∞—è –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è: <textarea style={{ width: '90%' }} autoFocus={true} onChange={onAddInfoChange}
      value={kassirInfo} />
    –ò—Å—Ç–æ—á–Ω–∏–∫–∏/—Å—Å—ã–ª–∫–∏: <textarea style={{ width: '90%' }} autoFocus={true} onChange={onSoursesChange}
      value={sourses} />

{/* –ó–∞–≥—Ä—É–∑–∫–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π */}
    <div>
      <div>–ó–∞–≥—Ä—É–∑–∏—Ç–µ —Ñ–æ—Ç–æ 1</div>
      <input
        type="file"
        name="file"
        placeholder="Upload an image"
        onChange={uploadImage1}
      />
      {loading ? (
        <h3>Loading...</h3>
      ) : (
          <img src={image1} style={{ width: '100px' }} />
        )}
    </div>
    <div>
      <div>–ó–∞–≥—Ä—É–∑–∏—Ç–µ —Ñ–æ—Ç–æ 2</div>
      <input
        type="file"
        name="file"
        placeholder="Upload an image"
        onChange={uploadImage2}
      />
      {loading ? (
        <h3>Loading...</h3>
      ) : (
          <img src={image2} style={{ width: '100px' }} />
        )}
    </div>
{/* –ó–∞–≥—Ä—É–∑–∫–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π */}
{buttonDisable && <div><img src={Krutilka} /></div>}
    <Button variant="contained" color="primary" onClick={deactivateEditModeRequest} disabled ={buttonDisable}>
    –í–Ω–µ—Å—Ç–∏ –∏–∑–º–µ–Ω–µ–Ω–∏—è
</Button>
  </div>
}
export default AdminEditMode