import React from 'react'
import {useForm} from "react-hook-form";
import { Redirect } from 'react-router-dom';
import './Register.css'
import Krutilka from './../../../images/preloader.gif'
const RegistrationPage = ({registerMe, success, message, buttonDisable}) =>{
    const {register, handleSubmit, errors} = useForm({
        mode: 'onBlur'
    });

    const onSubmit = (data) => {
        console.log(data)
        registerMe(data.email, data.password, data.name, data.surname, data.patronic, data.telephone)
    };

    // if(success) return <Redirect to='/login' />



    return<>
    <div className='register'>
            <form onSubmit={handleSubmit(onSubmit)}>
                <p>–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä—É–π—Ç–µ—Å—å</p>
                {
                    (message && <span className='success'>{message}</span>)
                }
                <input ref={register((
                    {
                        required: 'Field is required',
                        pattern: {
                            value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                            message: "Invalid email address"
                        }
                    }))} name='email' placeholder='–µ-–º–µ–π–ª' className={errors.email ? 'input_error' : 'input'}
                       type="text"/>
                {errors.email && errors.email.message && <span className='loginError'>{errors.email.message}</span>}
                <input ref={register({required: true})} name='password' placeholder='–ø–∞—Ä–æ–ª—å'
                       className={errors.password ? 'input_error' : 'input'} type="password"/>
                {errors.password && <span className='loginError'>–ù–µ–æ–±—Ö–æ–¥–∏–º–æ –∑–∞–ø–æ–ª–Ω–∏—Ç—å –ø–æ–ª–µ</span>}

                <input ref={register({required: true})} name='name' placeholder='–≤–≤–µ–¥–∏—Ç–µ –∏–º—è'
                       className={errors.name ? 'input_error' : 'input'} type="name"/>
                {errors.name && <span className='loginError'>–ù–µ–æ–±—Ö–æ–¥–∏–º–æ –∑–∞–ø–æ–ª–Ω–∏—Ç—å –ø–æ–ª–µ</span>}


                <input ref={register({required: true})} name='surname' placeholder='–≤–≤–µ–¥–∏—Ç–µ —Ñ–∞–º–∏–ª–∏—é'
                       className={errors.surname ? 'input_error' : 'input'} type="surname"/>
                {errors.surname && <span className='loginError'>–ù–µ–æ–±—Ö–æ–¥–∏–º–æ –∑–∞–ø–æ–ª–Ω–∏—Ç—å –ø–æ–ª–µ</span>}

                <input ref={register({required: true})} name='patronic' placeholder='–≤–≤–µ–¥–∏—Ç–µ –æ—Ç—á–µ—Å—Ç–≤–æ'
                       className={errors.patronic ? 'input_error' : 'input'} type="patronic"/>
                {errors.patronic && <span className='loginError'>–ù–µ–æ–±—Ö–æ–¥–∏–º–æ –∑–∞–ø–æ–ª–Ω–∏—Ç—å –ø–æ–ª–µ</span>}

                <input ref={register({required: true})} name='telephone' placeholder='–≤–≤–µ–¥–∏—Ç–µ –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞'
                       className={errors.telephone ? 'input_error' : 'input'} type="telephone"/>
                {errors.telephone && <span className='loginError'>–ù–µ–æ–±—Ö–æ–¥–∏–º–æ –∑–∞–ø–æ–ª–Ω–∏—Ç—å –ø–æ–ª–µ</span>}
                {buttonDisable && <div><img src={Krutilka} /></div>}
                <button type='submit' className='btn' disabled={buttonDisable}>–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</button>
            </form>
        </div>
    </>
}
export default RegistrationPage