import React from 'react'
import {useForm, reset} from "react-hook-form";
import { Redirect, NavLink } from 'react-router-dom';
import './Login.css'
import Krutilka from './../../../images/preloader.gif'
import { useSelector } from 'react-redux';
const LoginPage = ({loginMe, success, message, buttonDisable}) =>{
    const userIsAuth = useSelector(store => store.loginPage.userIsAuth)
    const {register, handleSubmit, errors} = useForm({
        mode: 'onBlur'
    });

    const onSubmit = (data) => {
        console.log(data)
        loginMe(data.email, data.password)
    };

    // if(success) return <Redirect to='/catalog' />

    if(userIsAuth)return <Redirect to={"/catalog"}/>

    return<>
    <div className='register'>
            <form onSubmit={handleSubmit(onSubmit)}>
                <p>–í–æ–π–¥–∏—Ç–µ –≤ –∫–∞—Ç–∞–ª–æ–≥</p>
                {
                    (message && <span className='success'>{message}</span>)
                   
                }
                <input ref={register((
                    {
                        required: 'Field is required',
                        pattern: {
                            value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                            message: "Invalid email address"
                        }
                    }))} name='email' placeholder='–µ-–º–µ–π–ª' className={errors.email ? 'input_error' : 'input'}
                       type="text"/>
                {errors.email && errors.email.message && <span className='loginError'>{errors.email.message}</span>}
                <input ref={register({required: true})} name='password' placeholder='–ø–∞—Ä–æ–ª—å'
                       className={errors.password ? 'input_error' : 'input'} type="password"/>
                {errors.password && <span className='loginError'>–ù–µ–æ–±—Ö–æ–¥–∏–º–æ –∑–∞–ø–æ–ª–Ω–∏—Ç—å –ø–æ–ª–µ</span>}
                {buttonDisable && <div><img src={Krutilka} /></div>}
                <button type='submit' className='btn' disabled={buttonDisable}>–í–æ–π—Ç–∏</button>

                <div>–†–∞–Ω–µ–µ –Ω–µ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω—ã?<br/>–ü—Ä–æ–π–¥–∏—Ç–µ <NavLink to='./registration'>—Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—é</NavLink></div>
            </form>
        </div>
    </>
}
export default LoginPage