import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import change from './../../../images/change.png'
import Portal from '../portal/Portal';
import styles from './Modal.module.css';
import { SimpleSlider } from '../Slider/Slider';
import exit from './../../../images/exit.png'
import NumberInfoSandbox from '../NumberInfo/NumberInfoSandbox';
import { useSelector, useDispatch } from 'react-redux';
import { changeDescriptionCells } from '../../../redux/admin-catalog-reducer';
import ImageLoader from '../../imageLoader';
import AddingFormSandbox from '../AddingForm/AddingFormSandbox';
import Button from '@material-ui/core/Button';

import BigImageSliderContainer from './BigImageSlider/EachNumberInfoContainer.js';

const Modal = ({
  isOpen, onCancel, description
}) => {

  const userIsAuth = useSelector(store => store.loginPage.userIsAuth)
  const adminIsAuth = useSelector(store => store.loginPage.adminIsAuth)
  const userName = useSelector(store => store.loginPage.userName)
  const approved = useSelector(store => store.loginPage.approved)
  const dispatch = useDispatch()

  let [editMode, setEditMode] = useState(false)

  useEffect(() => {
    // setNewStatus(description.status)
    setNewCommon(description.common)
    setNewVarieties(description.varieties)
    setNewAstimation(description.astimation)
    setNewAddInfo(description.kassirInfo)
    setNewSourses(description.sourses)
  }, [description.common, description.varieties, description.astimation, description.kassirInfo, description.sourses])

  const activateEditMode = () => {
    setEditMode(true)
  }
  const deactivateEditMode = () => {
    setEditMode(false)
  }

  const deactivateEditModeRequest = () => {
    setEditMode(false)
    dispatch(changeDescriptionCells(description.id, description.year, description.nominal, description.number,description.upravl, common,varieties,astimation,kassirInfo,sourses, status))
  }

  let [status, setNewStatus] = useState(false)
  const onStatusChange = (e) => {
    setNewStatus(e.currentTarget.checked)
  }

  let [common, setNewCommon] = useState(description.common)
  const onCommonChange = (e) => {
    setNewCommon(e.currentTarget.value)
  }

  let [varieties, setNewVarieties] = useState(description.varieties)
  const onVarietiesChange = (e) => {
    setNewVarieties(e.currentTarget.value)
  }

  let [astimation, setNewAstimation] = useState(description.astimation)
  const onAstimationChange = (e) => {
    setNewAstimation(e.currentTarget.value)
  }

  let [kassirInfo, setNewAddInfo] = useState(description.kassirInfo)
  const onAddInfoChange = (e) => {
    setNewAddInfo(e.currentTarget.value)
  }


  let [sourses, setNewSourses] = useState(description.sourses)
  const onSoursesChange = (e) => {
    setNewSourses(e.currentTarget.value)
  }



  return (
    <>
      {isOpen &&
        <Portal>
          <div className={styles.modalOverlay}>
            <div className={styles.modalWindow}>
              <div className={styles.modalHeader}>
                <div className={styles.modalTitle}>{description.nominal} {description.nominal === 1 && '—Ä—É–±–ª—å'}{description.nominal === 3 && '—Ä—É–±–ª—è'}{description.nominal === 5 && '—Ä—É–±–ª–µ–π'}
                  {description.nominal === 10 && '—Ä—É–±–ª–µ–π'}{description.nominal === 25 && '—Ä—É–±–ª–µ–π'}{description.nominal === 50 && '—Ä—É–±–ª–µ–π'}{description.nominal === 100 && '—Ä—É–±–ª–µ–π'} {description.year} –≥–æ–¥–∞ {description.upravl}-{description.director}-{description.kassir}</div>
                <div onClick={onCancel}><img src={exit} /></div>
              </div>
              <div className={styles.modalBody}>
                <SimpleSlider photos={description.photos} />
                <BigImageSliderContainer item = {description}/>
                  {!editMode &&
                  <div >
                    <div><b>–û–ø–∏—Å–∞–Ω–∏–µ:{' '}</b>{description.common}</div>
                     <div><b>–†–∞–∑–Ω–æ–≤–∏–¥–Ω–æ—Å—Ç–∏:{' '}</b>{description.varieties}</div>
                     <div style = {{cursor: 'pointer', textDecoration: 'underline'}}><b><NumberInfoSandbox  description={description} name={"–ò–∑–≤–µ—Å—Ç–Ω—ã–µ –Ω–æ–º–µ—Ä–∞"} /></b></div>
                     <div><b>–û—Ä–∏–µ–Ω—Ç–∏—Ä–æ–≤–æ—á–Ω–∞—è —Å—Ç–æ–∏–º–æ—Å—Ç—å:{' '}</b>{description.astimation}</div>
                <div><b>–î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–∞—è –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è:{' '}</b>{description.kassirInfo}</div>
                <div><b>–ò—Å—Ç–æ—á–Ω–∏–∫–∏/—Å—Å—ã–ª–∫–∏:{' '}</b>{description.sourses}</div>
                {approved && userIsAuth && !adminIsAuth && <AddingFormSandbox description={description} sign ={<Button variant="contained" color="primary">–î–æ–±–∞–≤–∏—Ç—å –≤ –∫–∞—Ç–∞–ª–æ–≥</Button>} />}
                {adminIsAuth ? <div style = {{ display: 'flex', alignItems: 'center'}}><img src={change} style={{ width: '40px', cursor: 'pointer' }} onClick={activateEditMode} /> 
                                 
                              <ImageLoader description= {description}/>

                              </div>
                              : null}
                    
                     </div>

                  }
                  {editMode &&
                    <div >
                      <div><input type = "checkbox" checked={status} onChange = {onStatusChange}/> –°—Ç–∞—Ç—É—Å –∑–∞–ø–∏—Å–∏</div>
                      –û–ø–∏—Å–∞–Ω–∏–µ: <textarea style={{ width: '90%' }} autoFocus={true} onChange={onCommonChange}
                        value={common} />
                      –†–∞–∑–Ω–æ–≤–∏–¥–Ω–æ—Å—Ç–∏: <textarea style={{ width: '90%' }} autoFocus={true} onChange={onVarietiesChange}
                        value={varieties} />
                      –û—Ä–∏–µ–Ω—Ç–∏—Ä–æ–≤–æ—á–Ω–∞—è —Å—Ç–æ–∏–º–æ—Å—Ç—å: <textarea style={{ width: '90%' }} autoFocus={true} onChange={onAstimationChange}
                        value={astimation} />
                      –î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–∞—è –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è: <textarea style={{ width: '90%' }} autoFocus={true} onChange={onAddInfoChange}
                        value={kassirInfo} />
                      –ò—Å—Ç–æ—á–Ω–∏–∫–∏/—Å—Å—ã–ª–∫–∏: <textarea style={{ width: '90%' }} autoFocus={true} onChange={onSoursesChange}
                        value={sourses} />
                      <button onClick={deactivateEditModeRequest}>–í–Ω–µ—Å—Ç–∏ –∏–∑–º–µ–Ω–µ–Ω–∏—è</button>
                      <button onClick={deactivateEditMode}>–ó–∞–∫—Ä—ã—Ç—å</button>
                    </div>
                    }
              </div>
              <div className={styles.modalFooter}>
                <Button  variant="contained" color="primary" onClick={onCancel}>–ó–∞–∫—Ä—ã—Ç—å</Button>
              </div>
            </div>
          </div>
        </Portal>
      }
    </>
  );
};

Modal.propTypes = {
  title: PropTypes.string,
  info: PropTypes.string,
  isOpen: PropTypes.bool,
  onCancel: PropTypes.func,
  onSubmit: PropTypes.func,
  children: PropTypes.node,
};

Modal.defaultProps = {
  isOpen: true,
  onCancel: () => { },
  onSubmit: () => { },
  children: null,
};

export default Modal;
